import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import { Box, Button } from "@material-ui/core";

import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "fit-content",
    display: "flex",
    justifyContent: "flex-end",
    margin: "0 auto 6px",
  },
  buttonConteiner: {
    display: "grid",
    gridTemplateColumns: "repeat(3, auto)",
    gridAutoFlow: "dense",
  },
  button: {
    background: "white",
    color: "black",
    borderRadius: "0",
    border: "1px solid #35D69B",
    margin: "4px",
    fontSize: "14px",
    lineHeight: "17px",
    boxShadow: "none",
    padding: "9px 10px",
    backgroundColor: "rgba(53, 214, 155, 0.25)",
    "& span": {
      fontWeight: "400",
      textTransform: "none",
    },
    "&:hover": {
      boxShadow: "none",
      backgroundColor: "#35D69B",
    },
  },
  activeButton: {
    backgroundColor: "#35D69B",
    border: "1px solid rgba(53, 214, 155, 0.25)",
  },
}));

export const NavigationMenu = (props) => {
  const classes = useStyles();
  const handleChangeCategorie = (e) => {
    props.setCategorie(e);
  };
  console.log("aaaaaa", props);
  return (
    <Box className={classes.root}>
      <Box>
        <Button
          variant="contained"
          className={clsx(
            classes.button,
            props.categorie === null ? classes.activeButton : null
          )}
          onClick={() => handleChangeCategorie(null)}
        >
          Все работы
        </Button>
      </Box>
      <Box>
        <Box className={classes.buttonConteiner}>
          <Button
            variant="contained"
            className={clsx(
              classes.button,
              props.categorie === "polygraphy" ? classes.activeButton : null
            )}
            onClick={() => handleChangeCategorie("polygraphy")}
          >
            Полиграфия
          </Button>
          <Button
            variant="contained"
            className={clsx(
              classes.button,
              props.categorie === "largeFormatPrinting"
                ? classes.activeButton
                : null
            )}
            onClick={() => handleChangeCategorie("largeFormatPrinting")}
          >
            Широкоформатная печать
          </Button>
          <Button
            variant="contained"
            className={clsx(
              classes.button,
              props.categorie === "formStyle" ? classes.activeButton : null
            )}
            onClick={() => handleChangeCategorie("formStyle")}
          >
            Фирменный стиль
          </Button>
        </Box>
        <Box className={classes.buttonConteiner}>
          <Button
            variant="contained"
            className={clsx(
              classes.button,
              props.categorie === "branding" ? classes.activeButton : null
            )}
            onClick={() => handleChangeCategorie("branding")}
          >
            Брендирование
          </Button>
          <Button
            variant="contained"
            className={clsx(
              classes.button,
              props.categorie === "logo" ? classes.activeButton : null
            )}
            onClick={() => handleChangeCategorie("logo")}
          >
            Логотипы
          </Button>
          <Button
            variant="contained"
            className={clsx(
              classes.button,
              props.categorie === "multipagePrinting"
                ? classes.activeButton
                : null
            )}
            onClick={() => handleChangeCategorie("multipagePrinting")}
          >
            Многостраничная печать
          </Button>
        </Box>
      </Box>
    </Box>
  );
};
